body {
  background-image: url('backgroundImage.png');
  background-repeat: repeat;
  background-size: cover;
  height: 100vh;
  background-color: gray;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

table, th, td, tr {
  border: 2px solid black;
  text-align: center;
  margin: 20px;
  padding: 20px;
  background-color: white;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    "News"
    "ListGames";
}

.News {
  grid-area: News;
}

.ListGames {
  grid-area: ListGames;
}

.canvasGame {
  border: red solid 2px;
  display: block;
  margin: auto auto;
}

.canvasGame2 {
  border: green solid 2px;
}

#carouselExampleCaptions {
  width: 50%;
  margin: auto;
  padding: 10px;
}

.landingPage__funcionalities {
  width: 50%;
  margin: auto;
  padding: 10px;
}

.cartasGrid{
  display: grid;
  grid-template-columns: 33.3% 33.3% 33.3%; 
  margin-top: 5%;
  margin-left: 7.5%;

}
.gameCard{
  padding: 3%;
  width: 70%;
  height: 70%;
}

#gameCard__bannerImg{
  display: block;
  margin: auto;
  width: 100%;
  height: 45%;
}
.texto_verde {
  color: white  ;
  font-size: 20px;
}

.ranking_font_size {
  font-size: 20px;
}

.imagen_ranking {
  height: 30px
}

